:host {
  @apply flex items-center w-full;

  ::ng-deep {
    .p-inputtext {
      color: var(--form-field-color) !important;
      background: var(--p-inputtext-background) !important;
      border: 1px solid var(--p-inputtext-border-color) !important;
      &:enabled {
        &:focus {
          border-color: #378bc0 !important;
        }
      }
    }

    .p-floatlabel:has(input:-webkit-autofill) label,
    .p-floatlabel:has(input:focus) label {
      color: #378bc0;
    }

    p-select {
      background: var(--p-select-background) !important;
      border-color: var(--p-select-border-color) !important;
      .p-select-label {
        color: var(--form-field-color) !important;
      }
    }
  }
}

.panel {
  @screen sm {
    @apply h-screen p-0 mb-0 bottom-0;
  }
}

.login-box {
  @screen sm {
    @apply w-full h-full top-0 p-0;
  }
}
